import React, { useState, useEffect, Fragment } from "react";

const url =
  "https://raw.githubusercontent.com/Coding-Forest/2021-React/main/Mini%20Project%203%20South%20African%20Animal%20Atlas/animal.json";

const App = () => {
  const [animals, setAnimals] = useState([]);

  const getAnimals = async () => {
    const response = await fetch(url);
    const animals = await response.json();
    setAnimals(animals);
  };

  useEffect(() => {
    getAnimals();
  }, []);

  return (
    <Fragment className="container">
      <h1 className="title">Animals of South Africa</h1>
      <p className='title'>
        Welcome to the Atlas of Animals of South Africa. <br />
        I'm building this catalogue as part of my front-end portfolio. All
        images and animal data are from{" "}
        <a
          href="https://a-z-animals.com/animals/location/africa/south-africa/"
          target="_blank">
          AZ Animals
        </a>
      </p>
      <ul className="catalogue">
        {animals.map((animal) => {
          const { name, fun_fact, image_url, page_url } = animal;
          console.log(animal);
          return (
            <li id={name} className="card">
              <div className="card-body">
                <a href={page_url}>
                  <img
                    className="card-image"
                    src={image_url}
                    loading="lazy"
                    alt={name}
                  />
                </a>
                <div className="card-caption">
                  <h4 className="card-title">
                    <a href={page_url} target="_blank">
                      {name}
                    </a>
                  </h4>
                  <p>{fun_fact}</p>
                </div>
              </div>
            </li>
          );
        })}
      </ul>
    </Fragment>
  );
};

export default App;
